import React, { useState } from 'react'
import styled from 'styled-components'
import ReactPlayer from 'react-player'
import { connect } from 'react-redux'
// import firebase from 'firebase'
import { PostArticleAPI } from '../actions'
const firebase = require('firebase')
const PostModal = (props) => {
  const [editorText, setEditorText] = useState('')
  const [shareImage, setShareImage] = useState('')
  const [videoLink, setVideoLink] = useState('')
  const [assetArea, setassetArea] = useState('')

  const reset = (e) => {
    setEditorText(' ')
    setShareImage('')
    setVideoLink('')
    setassetArea('')
    props.handleClick(e)
    // console.log(props.handleClick)
  }
  const handleImageChange = (e) => {
    const image = e.target.files[0]
    if (image === '' || image === undefined) {
      alert(`Not an image, the file Type is ${typeof image}`)
      return
    }
    setShareImage(image)
  }
  const switchAssetArea = (area) => {
    setShareImage('')
    setVideoLink('')
    setassetArea(area)
  }
  const postArticle = (e) => {
    e.preventDefault()
    if (e.target !== e.currentTarget) {
      return
    }
    const payload = {
      image: shareImage,
      video: videoLink,
      user: props.user,
      description: editorText,
      timestamp: new Date(),
    }
    props.postArticle(payload)
    console.log(payload)
    reset(e)
  }
  return (
    <>
      {props.showModel === 'open' && (
        <Container>
          <Content>
            <Header>
              <h2>Create Post</h2>
              <button onClick={(e) => reset(e)}>
                <img src="/images/close.png" alt="" srcset="" />
              </button>
            </Header>
            <Sharedinfo>
              <UserInfo>
                {props.user.photoUrl ? (
                  <img src={props.user.photoUrl} />
                ) : (
                  <img src="/images/user.svg" alt="" />
                )}

                <span>{props.user.displayName}</span>
              </UserInfo>
              <Editor>
                <textarea
                  name=""
                  id=""
                  cols="30"
                  rows="2"
                  value={editorText}
                  placeholder="What you want to talk about?"
                  autoFocus={true}
                  onChange={(e) => setEditorText(e.target.value)}
                />
                {assetArea === 'image' ? (
                  <UploadImage>
                    <input
                      type="file"
                      accept="image/gif image/jpeg image/png image/jpg"
                      name="image"
                      id="file"
                      style={{ display: 'none' }}
                      onChange={handleImageChange}
                    />
                    <p>
                      <label htmlFor="file">
                        <u>Click</u> to Share Image ðŸ“¸
                      </label>
                    </p>
                    {shareImage && (
                      <img src={URL.createObjectURL(shareImage)} />
                    )}
                  </UploadImage>
                ) : (
                  assetArea === 'media' && (
                    <>
                      <input
                        type="text"
                        placeholder="Place your Video link here"
                        value={videoLink}
                        onChange={(e) => setVideoLink(e.target.value)}
                      />
                      {videoLink && (
                        <ReactPlayer width={'100%'} url={videoLink} />
                      )}
                    </>
                  )
                )}
              </Editor>
            </Sharedinfo>
            <SharedCreation>
              <AttachAsset>
                <AssetBtn>
                  <img
                    src="/images/photo-icon.png"
                    alt=""
                    onClick={() => setassetArea('image')}
                  />
                  <span>Add Photo</span>
                </AssetBtn>
                <AssetBtn>
                  <img
                    src="/images/video-icon.png"
                    alt=""
                    onClick={() => setassetArea('media')}
                  />
                  <span>Add Video</span>
                </AssetBtn>
              </AttachAsset>
              <ShareComment>
                <AssetBtn>
                  <img src="/images/comment.png" alt="" />
                  <a>Anyone</a>
                </AssetBtn>
                <PostBtn
                  disabled={!editorText ? true : false}
                  onClick={(e) => postArticle(e)}
                >
                  Post
                </PostBtn>
              </ShareComment>
            </SharedCreation>
          </Content>
        </Container>
      )}
    </>
  )
}
const Container = styled.div`
  position: fixed;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  z-index: 99999;
  background-color: rgba(0, 0, 0, 0.8);
  animation: fadeIn 0.5s;
`
const Content = styled.div`
  max-width: 552px;
  width: 100%;
  background-color: white;
  max-height: 90%;
  overflow: intial;
  border-radius: 5px;
  position: relative;
  display: flex;
  flex-direction: column;
  top: 32px;
  margin: 0 auto;
`
const Header = styled.div`
  display: flex;
  padding: 16px 20px;
  border-bottom: 1px solid rgba(0 0 0 0.8);
  font-size: 16px;
  line-height: 1.5;
  color: rgba(0 0 0 0.8);
  justify-content: space-between;
  align-items: center;
  button {
    background-color: white;
    border: none;
  }
`
const Sharedinfo = styled.div`
  display: flex;
  flex-direction: column;
  flex-grow: 1;
  overflow-y: auto;
  vertical-align: baseline;
  background: white;
  padding: 8px 12px;
`

const UserInfo = styled.div`
  display: flex;
  align-items: center;
  padding: 12px 24px;
  svg,
  img {
    width: 48px;
    height: 48px;
    background-clip: content-box;
    border-radius: 50%;
  }
  span {
    font-weight: 600;
    font-size: 16px;
    line-height: 1.5;
    margin-left: 5px;
  }
`

const SharedCreation = styled.div`
  display: flex;
  justify-content: space-between;
  padding: 12px 24px;
`
const AssetBtn = styled.button`
  display: flex;
  align-items: center;
  height: 40px;
  min-width: auto;
  color: rgba(0, 0 0 0.5);
  background-color: white;
  /* outline: none; */
  border: none;
  margin-right: 5px;
  text-decoration: dashed;
  &:hover {
    background: rgba(0 0 0 0.5);
  }
`
const AttachAsset = styled.div`
  align-items: center;
  display: flex;
  padding-right: 8px;
`
const ShareComment = styled.div`
  display: flex;
  justify-content: space-between;
  padding: 12px 24px;
`
const PostBtn = styled.button`
  min-width: 60px;
  border-radius: 20px;
  padding: 0 16px;
  background: ${(props) => (props.disabled ? '#6D6D6D' : '#0a66c2')};
  color: ${(props) => (props.disabled ? '#414141' : 'white')};
  animation: FadeIn 0.3s;
  &:hover {
    background: #004182;
  }
`
const Editor = styled.div`
  padding: 12px 24px;
  textarea {
    width: 100%;
    min-height: 100px;
    resize: none;
  }
  input {
    width: 100%;
    height: 35px;
    font-size: 16px;
    margin-bottom: 20px;
  }
`
const UploadImage = styled.div`
  text-align: center;
  img {
    object-fit: contain;
    max-width: 200px;
    margin: 5px;
  }
`
const mapStateToProps = (state) => {
  return {
    user: state.userState.user,
  }
}
const mapDispatchToProps = (dispatch) => ({
  postArticle: (payload) => dispatch(PostArticleAPI(payload)),
})

export default connect(mapStateToProps, mapDispatchToProps)(PostModal)
