import React, { useEffect, useState } from 'react'
import { connect } from 'react-redux'
import styled from 'styled-components'
import PostModal from './PostModal'
import { getArticleAPI } from '../actions'
import ReactPlayer from 'react-player'
const Main = (props) => {
  const [showModel, setShowModel] = useState('close')
  const handleClick = (e) => {
    e.preventDefault()

    switch (showModel) {
      case 'open':
        setShowModel('close')
        break
      case 'close':
        setShowModel('open')
        break
      default:
        setShowModel('close')
        break
    }
  }
  useEffect(() => {
    props.getArticle()
  }, [getArticleAPI])
  return (
    <>
      <Container>
        <Sharebox>
          {/* Share */}

          <div>
            {props.user && props.user.photoUrl ? (
              <img src={props.user.photoUrl} alt="" />
            ) : (
              <img src="/images/user.svg" alt="" />
            )}

            <button
              onClick={handleClick}
              disabled={props.loading ? true : false}
            >
              Start Post
            </button>
          </div>
          <div>
            <button>
              <img src="/images/photo-icon.png" alt="" />
              <span>Photo</span>
            </button>
            <button>
              <img src="/images/video-icon.png" alt="" />
              <span>Video</span>
            </button>
            <button>
              <img src="/images/event-icon.png" alt="" />
              <span>Event</span>
            </button>
            <button>
              <img src="/images/article-icon.png" alt="" />
              <span>Write Article</span>
            </button>
          </div>
        </Sharebox>
        {props.articles.length === 0 ? (
          <p> There are No articles </p>
        ) : (
          <Content>
            {props.loading && <img src="/images/spin.svg" />}
            {props.articles.length > 0 &&
              props.articles.map((article, key) => {
                return (
                  <Article key={key}>
                    <SharedActor>
                      <a href="">
                        <img src="./images/user.svg" alt="" />
                        <div>
                          <span>{article.actor.title}</span>
                          <span>{article.actor.description}</span>
                          <span>
                            {article.actor.date.toDate().toLocaleDateString()}
                          </span>
                        </div>
                      </a>
                      <button>
                        <img src="/images/elipses.svg" alt="" />
                      </button>
                    </SharedActor>
                    <Description>{article.description}</Description>
                    <SharedImage>
                      {!article.image && article.video ? (
                        <ReactPlayer width={'100%'} url={article.video} />
                      ) : (
                        article.image && <img src={article.image} />
                      )}
                    </SharedImage>
                    <SocialCounts>
                      <li>
                        <button>
                          <img src="/images/like.png" alt="" />
                          <img src="/images/clap.png" alt="" />
                          <span>75</span>
                        </button>
                      </li>
                      <li>
                        <a href="">{article.comments} comments</a>
                      </li>
                    </SocialCounts>
                    <SocialAction>
                      <button>
                        <img src="/images/bluelike.png" alt="" />
                        <span>Like</span>
                      </button>
                      <button>
                        <img src="/images/comment.png" alt="" />
                        <span>Comment</span>
                      </button>
                      <button>
                        <img src="/images/share.png" alt="" />
                        <span>Share</span>
                      </button>
                      <button>
                        <img src="/images/send.png" alt="" />
                        <span>Send</span>
                      </button>
                    </SocialAction>
                  </Article>
                )
              })}
          </Content>
        )}
        <PostModal showModel={showModel} handleClick={handleClick} />
      </Container>
    </>
  )
}
const Container = styled.div`
  grid-area: main;
`
const CommanCard = styled.div`
  text-align: center;
  overflow: hidden;
  margin-bottom: 8px;
  background: #fff;
  border-radius: 5px;
  position: relative;
  border: none;
  box-shadow: rgba(0, 0, 0, 0.24) 0px 3px 8px;
`
const Sharebox = styled(CommanCard)`
  display: flex;
  flex-direction: column;
  color: #958b7b;
  margin: 0 0 8px;
  background: white;
  div {
    button {
      outline: none;
      color: rgba(0, 0, 0, 0.6);
      font-size: 14px;
      line-height: 1.5;
      min-height: 48px;
      background: white;
      border: none;
      display: flex;
      align-items: center;
      font-weight: 600;
      img {
        width: 20px;
      }
    }
    &:first-child {
      display: flex;
      align-items: center;
      padding: 8px 16px 0px 8px;
      img {
        width: 48px;
        border-radius: 50%;
        margin-right: 8px;
      }
      button {
        margin: 4px 0;
        flex-grow: 1;
        border-radius: 35px;
        padding-left: 16px;
        border: 1px solid rgba(0, 0, 0, 0.15);
        border-radius: 35px;
        background-color: white;
        text-align: left;
      }
    }
    &:nth-child(2) {
      display: flex;
      flex-wrap: wrap;
      justify-content: space-around;
      padding-bottom: 4px;
      button {
        img {
          margin: 0 4px 0 -2px;
        }
        span {
          color: #70b5f9;
        }
      }
    }
  }
`
const Article = styled(CommanCard)`
  padding: 0;
  margin: 0 0 8px;
  overflow: visible;
`
const SharedActor = styled.div`
  padding-right: 40px;
  flex-wrap: nowrap;
  padding: 12px 16px 0;
  margin-bottom: 8px;
  align-items: center;
  display: flex;
  a {
    text-decoration: none;
    margin-right: 12px;
    color: black;
    flex-grow: 1;
    overflow: hidden;
    display: flex;
    img {
      height: 48px;
      width: 48px;
    }
    & > div {
      display: flex;
      flex-direction: column;
      flex-grow: 1;
      flex-basis: 0;
      margin-left: 8px;
      overflow: hidden;
      span {
        text-align: left;
        display: flex;
        flex-direction: column;
        &:first-child {
          font-size: 14px;
          font-weight: 700;
          color: rgba(0 0 0 1);
        }
        &:nth-child(n + 1) {
          font-size: 12px;
          color: rgba(0 0 0 0.6);
        }
      }
    }
  }
  button {
    position: absolute;
    top: 12px;
    right: 0;
    background: white;
    border: none;
    outline: none;
    img {
      width: 20px;
    }
  }
`

const Description = styled.div`
  padding: 0 16px;
  overflow: hidden;
  color: rgba(0, 0, 0, 0.9);
  font-size: 14px;
  text-align: left;
`
const SharedImage = styled.div`
  margin-top: 8px;
  width: 100%;
  display: block;
  position: relative;
  background: #f9fafb;
  img {
    object-fit: contain;
    width: 100%;
    height: 100%;
  }
`

const SocialCounts = styled.div`
  line-height: 1.3;
  display: flex;
  align-items: flex-start;
  /* justify-content: center; */
  overflow: auto;
  margin: 0 16px;
  padding: 8px 0;
  border-bottom: 1px solid #e9e5df;
  list-style: none;
  li {
    margin-right: 5px;
    font-size: 12px;
    button {
      display: flex;
      align-items: center;
      justify-content: center;
      border: none;
      background-color: #fff;
      img {
        height: 17px;
        width: 17px;
      }
    }
    a {
      text-decoration: none;
      color: black;
    }
  }
`
const SocialAction = styled.div`
  align-items: center;
  display: flex;
  justify-content: flex-start;
  margin: 0;
  min-height: 40px;
  padding: 4px 8px;
  button {
    display: inline-flex;
    align-items: center;
    padding: 8px;
    color: #0a66c2;
    outline: none;
    border: none;
    background-color: white;
    img {
    }
    span {
      margin-left: 4px;
    }
  }
`
const Content = styled.div`
  text-align: center;
  & > img {
    width: 30px;
    object-fit: contain;
  }
`
const mapStateToProps = (state) => {
  return {
    loading: state.articleState.loading,
    user: state.userState.user,
    articles: state.articleState.articles,
  }
}

const mapDispatchToProps = (dispatch) => ({
  getArticle: () => dispatch(getArticleAPI()),
})
export default connect(mapStateToProps, mapDispatchToProps)(Main)
